import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-add-pickup-address-ahamove',
  templateUrl: './add-pickup-address-ahamove.component.html',
  styleUrls: ['./add-pickup-address-ahamove.component.scss']
})
export class AddPickupAddressAhamoveComponent implements OnInit {
  @Input("addPickupAddressAhamove") addPickupAddressAhamove: boolean = false;
  @Input("dataAddPickupAddressAhamove") id_delivery;
  @Output("closeAddPickupAddressAhamove") closeAddPickupAddressAhamove = new EventEmitter<any>();
  constructor(
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales
  ) {
  }
  addInformation: FormGroup;
  ngOnInit() {
    this.initForm();
  }
  initForm() {
    this.addInformation = new FormGroup({
      name: new FormControl('', [Validators.required, Validators.minLength(6)]),
      phone: new FormControl('', [Validators.required]),
      address: new FormControl('', Validators.required),
      province: new FormControl('', Validators.required),
      district: new FormControl('', Validators.required),
      ward: new FormControl('', Validators.required),
    })
  }
  addInfoPickup() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to create pickup information ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.addAddress(this.preparePickupAddress(this.id_delivery)).then((res: any) => {
            this.vhComponent.hideLoading(0).then(() => {
              if (res) {
                this.vhComponent.showToast(2000, this.languageService.translate("Create pickup information successfully"), "success-toast");
                this.closeAddPickupAddressAhamove.emit(true);
              }
            })
          })
        })
      }).catch(() => { })
  }
  preparePickupAddress(id) {
    let data = {
      name: this.addInformation.value.name,
      phone: this.addInformation.value.phone,
      province: this.addInformation.value.province,
      district: this.addInformation.value.district,
      ward: this.addInformation.value.ward,
      address: this.addInformation.value.address,
      id_delivery: id,
      type: 4, // địa chỉ lấy hàng ahamove 
      date: new Date().toISOString()
    }
    return data;
  }
  goBack() {
    this.closeAddPickupAddressAhamove.emit(false);
  }

}
