import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-list-pickup-address-ahamove',
  templateUrl: './list-pickup-address-ahamove.component.html',
  styleUrls: ['./list-pickup-address-ahamove.component.scss']
})
export class ListPickupAddressAhamoveComponent implements OnInit {

  @Input("pickupAdresssAhamove") pickupAdresssAhamove: boolean = false;
  @Input("dataPickupAddressAhamove") id_delivery;
  @Output("cancelListPickupAddressAhamove") cancelListPickupAddressAhamove = new EventEmitter<any>();
  constructor(
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    public vhAlgorighm: VhAlgorithm,
    private vhQuerySales: VhQuerySales
  ) {

  }
  infoAddress: any = [];
  status_open: boolean = false;
  ngOnInit() {
    this.getData();
  }
  getData() {
    this.status_open = true;
    this.vhQuerySales.refreshLocalAddresss().then(() => {
      let addresss = this.vhQuerySales.getlocalAddresss().filter(item => item.type == 4 && item.id_delivery == this.id_delivery);
      if (addresss.length) {
        this.infoAddress = addresss.reverse();

      } else this.infoAddress = []
    })
  }
  goBack() {
    if (this.infoAddress.length) this.cancelListPickupAddressAhamove.emit(false);
    else this.cancelListPickupAddressAhamove.emit({ _id: '' });

  }
  chooseAddress(item) {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to use this pickup informartion ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.updateAddress(item._id, { date: new Date().toISOString() }).then((bool) => {
            this.vhComponent.hideLoading(0).then(() => {
              this.cancelListPickupAddressAhamove.emit(item);
            })
          })
        })
      })
      .catch(() => { })
  }
  // -----------------modal add address pickup ahamove----------------
  addPickupAddressAhamove: boolean = false;
  dataAddPickupAddressAhamove: any;
  addAddress() {
    this.addPickupAddressAhamove = true;
    this.dataAddPickupAddressAhamove = this.id_delivery;
  }
  closeAddPickupAddressAhamove(event) {
    if (event) {
      this.getData();
    }
    this.addPickupAddressAhamove = false;
  }
  // -----------------modal edit address pickup ahamove----------------
  editPickupAddressAhamove: boolean = false;
  dataEditPickupAddressAhamove: any;
  editAddress(item) {
    this.editPickupAddressAhamove = true;
    this.dataEditPickupAddressAhamove = item
  }
  closeEditPickupAddressAhamove(event) {
    if (event) {
      this.getData();
    }
    this.editPickupAddressAhamove = false;
  }
}
