import { Component, Input, OnInit } from '@angular/core';
import { Output, EventEmitter } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { GhnService } from 'src/app/sales/menu/manage/delivery/services/ghnService/ghn.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ThemeService } from 'src/app/sales/services/theme.service';
@Component({
  selector: 'app-connect-ghn',
  templateUrl: './connect-ghn.component.html',
  styleUrls: ['./connect-ghn.component.scss']
})
export class ConnectGhnComponent implements OnInit {
  token: string = '';
  @Input("connectGhn") connectGhn: boolean = false;
  @Output() cancelModal = new EventEmitter<any>();
  constructor(
    private ghnService: GhnService,
    private vhComponent: VhComponent,
    private lannguageService: LanguageService,
    public themeService: ThemeService,
  ) { }

  ngOnInit(): void {
  }
  handleCancel() {
    this.cancelModal.emit({
      connectGhn: false,
      shops: [],
      token: ""
    });
  }
  checkTokenGhn(token) {
    if (token) {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.ghnService.connectTokenGhn(token).then((res: any) => {
          this.vhComponent.hideLoading(0).then(() => {
            if (res.code == 200) {
              this.cancelModal.emit({
                connectGhn: true,
                shops: res.data.shops,
                token: token
              });
            }
          })
        }).catch(err => {
          this.vhComponent.hideLoading(0).then(() => {
            this.vhComponent.showToast(2000, this.lannguageService.translate('Invalid token'), "alert-toast");
          })
        })
      })
    } else {
      this.vhComponent.showToast(2000, this.lannguageService.translate('Token is required'), "alert-toast")
    }
  }
}
