import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { AhamoveService } from 'src/app/sales/menu/manage/delivery/services/ahamoveService/ahamove.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ThemeService } from 'src/app/sales/services/theme.service';

@Component({
  selector: 'app-detail-warehouse-ahamove',
  templateUrl: './detail-warehouse-ahamove.component.html',
  styleUrls: ['./detail-warehouse-ahamove.component.scss']
})
export class DetailWarehouseAhamoveComponent implements OnInit {
  @Input("detailWarehouseAhamove") detailWarehouseAhamove: boolean = false;
  @Input("dataDetailWarehouseAhamove") dataDetailWarehouseAhamove;
  @Output("cancelDetailWarehouseAhamove") cancelDetailWarehouseAhamove = new EventEmitter<any>();
  constructor(
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhQuerySales: VhQuerySales,
    private vhAlgorithm: VhAlgorithm,
    public themeService: ThemeService,
  ) {

  }
  isTrue: boolean = true;
  id_delivery = '';
  info_pickup: any = {
    id_delivery: ""
  };
  status_open: boolean = false;
  ngOnInit() {
    this.getData();
  }
  getData() {
    this.status_open = true;
    this.vhQuerySales.refreshLocalAddresss().then(() => {
      let addresss: Array<any> = this.vhQuerySales.getlocalAddresss().filter(item => item.type == 4 && item.id_delivery == this.dataDetailWarehouseAhamove._id);
      if (addresss.length)
        this.info_pickup = addresss.sort((a, b) => Date.parse(b.date) - Date.parse(a.date))[0]
    })
  }
  disconnetAhamove() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to disconnect ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.updateDelivery(this.dataDetailWarehouseAhamove._id, { "active": false }).then((res) => {
            this.vhComponent.hideLoading(0).then(() => {
              this.cancelDetailWarehouseAhamove.emit(true)
              this.vhComponent.showToast(2000, this.languageService.translate('Disconnect transport successfully !'), "success-toast");
            })
          })
        })
      })
      .catch(() => { })
  }
  connectAhamove() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to connect warehouse ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.updateDelivery(this.dataDetailWarehouseAhamove._id, { "active": true }).then((res) => {
            this.vhComponent.hideLoading(0).then(() => {
              if (res) {
                this.vhComponent.showToast(2000, this.languageService.translate("Connect successfully"), "success-toast");
                this.cancelDetailWarehouseAhamove.emit(true)
              }
            })
          })
        })
      }).catch(() => { })
  }
  handleCancel() {
    this.cancelDetailWarehouseAhamove.emit(false);
  }
  // ----------modal list address pickup---------
  pickupAdresssAhamove: boolean = false;
  dataPickupAddressAhamove: any;
  gotoAddressPickup() {
    this.pickupAdresssAhamove = true;
    this.dataPickupAddressAhamove = this.dataDetailWarehouseAhamove._id;
  }
  cancelListPickupAddressAhamove(event) {
    if (event && event._id) this.info_pickup = event;
    else if (event && !event._id) this.info_pickup.id_delivery = '';
    this.pickupAdresssAhamove = false;
  }
}
