import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { AhamoveService } from 'src/app/sales/menu/manage/delivery/services/ahamoveService/ahamove.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ThemeService } from 'src/app/sales/services/theme.service';

@Component({
  selector: 'app-connect-ahamove',
  templateUrl: './connect-ahamove.component.html',
  styleUrls: ['./connect-ahamove.component.scss']
})
export class ConnectAhamoveComponent implements OnInit {

  token: any = ''
  @Input("connectAhamove") connectAhamove: boolean = false;
  @Input("dataConnectAhamove") dataConnectAhamove;
  @Output() cancelModalConnectAhamvoe = new EventEmitter<any>();
  constructor(
    private ahamoveService: AhamoveService,
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private vhAlgorithm: VhAlgorithm,
    public themeService: ThemeService,
  ) { }
  isTrue: boolean = true;
  list_ahamove: any = [];
  data_ahamove: any = {};
  ngOnInit() {
    this.list_ahamove = this.dataConnectAhamove.list_ahamove;
    this.data_ahamove = this.dataConnectAhamove.data_ahamove;
  }
  ngAfterViewInit() {
    this.token = this.vhAlgorithm.vhphoneNumber(".phone");
  }
  checkToken() {
    let phone = this.token.getRawValue()
    let check = this.checkExistPhoneNumber(phone);
    this.checkPhone(phone);
    if (this.isTrue) {
      if (check.isExist) {
        this.cancelModalConnectAhamvoe.emit(check);
      }
      else { // trường hợp số điện thoại chưa tồn tại
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.ahamoveService.getTokenAhaMove(phone).then((res: any) => {
            this.vhComponent.hideLoading(0).then(() => {
              if (res) {
                this.cancelModalConnectAhamvoe.emit({ phone: phone });
              }
            })
          }).catch(err => {
            this.vhComponent.hideLoading(0);
            this.vhComponent.showToast(2000, this.languageService.translate("Phone number invalid, please try again"), "alert-toast")
          })
        })
      }
    }
  }
  checkExistPhoneNumber(phone) {
    let check: any = {
      isExist: false
    }
    for (let i in this.list_ahamove) {
      if (this.list_ahamove[i].phone == phone) {
        check = {
          ...this.list_ahamove[i],
          isExist: true,
        }
        break;
      }
    }
    return check;
  }
  checkPhone(phone) {
    if (phone.length > 7) this.isTrue = true;
    else this.isTrue = false;
  }
  handleCancel() {
    this.cancelModalConnectAhamvoe.emit(false);
  }
}
