import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-edit-pickup-address-ahamove',
  templateUrl: './edit-pickup-address-ahamove.component.html',
  styleUrls: ['./edit-pickup-address-ahamove.component.scss']
})
export class EditPickupAddressAhamoveComponent implements OnInit {
  @Input("editPickupAddressAhamove") editPickupAddressAhamove: boolean = false;
  @Input("dataEditPickupAddressAhamove") data_pickup;
  @Output("closeEditPickupAddressAhamove") closeEditPickupAddressAhamove = new EventEmitter<any>();
  constructor(
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales
  ) {
  }
  addInformation: FormGroup;
  openStatus: boolean = false;
  ngOnInit() {
    this.initForm();
  }
  initForm() {
    this.addInformation = new FormGroup({
      name: new FormControl(this.data_pickup.name, [Validators.required, Validators.minLength(6)]),
      phone: new FormControl(this.data_pickup.phone, [Validators.required]),
      address: new FormControl(this.data_pickup.address, Validators.required),
      province: new FormControl(this.data_pickup.province, Validators.required),
      district: new FormControl(this.data_pickup.district, Validators.required),
      ward: new FormControl(this.data_pickup.ward, Validators.required),
    })
  }
  preparePickupAddress(id) {
    let data = {
      name: this.addInformation.value.name,
      phone: this.addInformation.value.phone,
      province: this.addInformation.value.province,
      district: this.addInformation.value.district,
      ward: this.addInformation.value.ward,
      address: this.addInformation.value.address,
      id_delivery: id,
      type: 4, // địa chỉ lấy hàng ahamove 
    }
    return data;
  }
  goBack() {
    this.closeEditPickupAddressAhamove.emit(false);
  }
  updatePickupInformation() {
    if (!this.addInformation.invalid) {
      this.vhQuerySales.updateAddress(this.data_pickup._id, this.preparePickupAddress(this.data_pickup.id_delivery)).then(() => {
        this.closeEditPickupAddressAhamove.emit(true);
      }).catch(err => {
        console.error("err");
      })
    }
  }
  deketePickupInformation() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to delete pickup information ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.deleteAddress(this.data_pickup._id).then((res) => {
            this.vhComponent.hideLoading(0).then(() => {
              this.vhComponent.showToast(2000, this.languageService.translate("Delete pickup information successfully"), "success-toast");
              this.closeEditPickupAddressAhamove.emit(true);
            })
          })
        })
      }).catch(() => { })
  }

}
