import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ThemeService } from 'src/app/sales/services/theme.service';

@Component({
  selector: 'app-choose-warehouse-ahamove',
  templateUrl: './choose-warehouse-ahamove.component.html',
  styleUrls: ['./choose-warehouse-ahamove.component.scss']
})
export class ChooseWarehouseAhamoveComponent implements OnInit {

  @Input("chooseWarehouseAhamove") chooseWarehouseAhamove: boolean = false;
  @Input("dataWarehouseChooseAhamove") dataWarehouseChooseAhamove;
  @Output() cancelModalChooseWarehouse = new EventEmitter<any>();
  constructor(
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private vhQuerySales: VhQuerySales,
    public themeService: ThemeService,
  ) {

  }
  addInformation: FormGroup;
  phone: any = '';
  ngOnInit() {
    this.phone = this.dataWarehouseChooseAhamove.phone;
    this.initForm();
  }
  initForm() {
    this.addInformation = new FormGroup({
      name: new FormControl('', [Validators.required, Validators.minLength(6)]),
      phone: new FormControl('', [Validators.required]),
      address: new FormControl('', Validators.required),
      province: new FormControl('', Validators.required),
      district: new FormControl('', Validators.required),
      ward: new FormControl('', Validators.required),
    })

  }
  connectAhamove() {
    this.vhComponent.alertConfirm("", this.languageService.translate("Do you want to connect warehouse ?"), "", "Ok", this.languageService.translate("Cancel"))
      .then(() => {
        this.vhComponent.showLoading("", "transparent-loading").then(() => {
          this.vhQuerySales.addDelivery(this.prepareDelivery()).then((res: any) => {
            this.vhQuerySales.addAddress(this.preparePickupAddress(res._id)).then((result: any) => {
              this.vhComponent.hideLoading(0).then(() => {
                if (result) {
                  this.vhComponent.showToast(2000, this.languageService.translate("Connect successfully"), "success-toast");
                  this.cancelModalChooseWarehouse.emit(true)
                }
              })
            })
          })
        })
      }).catch(() => { })
  }
  preparePickupAddress(id) {
    let data = {
      name: this.addInformation.value.name,
      phone: this.addInformation.value.phone,
      province: this.addInformation.value.province,
      district: this.addInformation.value.district,
      ward: this.addInformation.value.ward,
      address: this.addInformation.value.address,
      id_delivery: id,
      type: 4,//type 4 là địa chỉ lấy hàng ahamove
      date: new Date().toISOString()
    }
    return data;
  }
  prepareDelivery() {
    let data = {
      type: 3, // type = 3 là ahamove
      active: true,
      phone: this.phone,
      name: "Ahamove"
    }
    return data;
  }
  handleCancel() {
    this.cancelModalChooseWarehouse.emit(false)
  }
}
