import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { GhtkService } from 'src/app/sales/menu/manage/delivery/services/ghtkService/ghtk.service';
import { LanguageService } from 'src/app/sales/services/language.service';
import { ThemeService } from 'src/app/sales/services/theme.service';

@Component({
  selector: 'app-connect-ghtk',
  templateUrl: './connect-ghtk.component.html',
  styleUrls: ['./connect-ghtk.component.scss']
})
export class ConnectGhtkComponent implements OnInit {

  token: string = '';
  @Input("connectGhtk") connectGhtk: boolean = false;
  @Output("closeAndGetShopAndTokenGhtk") closeAndGetShopAndTokenGhtk = new EventEmitter<any>();
  constructor(
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private ghtkService: GhtkService,
    public themeService: ThemeService,
  ) { }

  ngOnInit(): void {
  }
  handleCancel() {
    this.closeAndGetShopAndTokenGhtk.emit(false);
  }
  checkTokenGhtk(token) {
    if (token) {
      this.vhComponent.showLoading("", "transparent-loading").then(() => {
        this.ghtkService.getWareHouseGhtk(token).then((res: any) => {
          this.vhComponent.hideLoading(0).then(() => {
            this.closeAndGetShopAndTokenGhtk.emit({
              shops: res.data,
              token: token
            })
          })
        }).catch(err => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(2000, this.languageService.translate('Invalid token'), "alert-toast");
        })
      })
    } else {
      this.vhComponent.showToast(2000, this.languageService.translate('Token is required'), "alert-toast")
    }
  }

}
